import React from 'react';
import {Button, Form, Input, message, Modal} from 'antd';
import {SearchOutlined} from '@ant-design/icons';
import {searchGameByName} from '../utils';

class CustomSearch extends React.Component {
    state = {
        displayModal: false
    }

    handleCancel = () => {
        this.setState({
            displayModal: false,
        })
    }

    displayModal = () => {
        this.setState({
            displayModal: true,
        })
    }

    onSubmit = (data) => {
        searchGameByName(data.game_name)
            .then((data) => {
                console.log(data);
                console.log(this.props);
                this.setState({
                    displayModal: false,
                })
                // onSuccess={this.customSearchOnSuccess} passed from App.js
                this.props.onSuccess(data);
            })
            .catch((err) => {
                message.error(err.message);
            })
    }

    render = () => {
        return (
            <>
                <Button shape="round"
                        onClick={this.displayModal}
                        icon={<SearchOutlined/>}
                        style={{marginLeft: '20px', marginTop: '20px'}}>
                    Search Game
                </Button>
                <Modal
                    title="Search Game"
                    visible={this.state.displayModal}
                    onCancel={this.handleCancel}
                    footer={null}
                >
                    <Form
                        name="custom_search"
                        onFinish={this.onSubmit}
                    >
                        <Form.Item
                            name="game_name"
                            rules={[{required: true, message: 'Please enter a game name'}]}
                        >
                            <Input placeholder="Game name"/>
                        </Form.Item>
                        <Form.Item>
                            <Button type="primary" htmlType="submit">
                                Search
                            </Button>
                        </Form.Item>
                    </Form>
                </Modal>
            </>
        )
    }
}

export default CustomSearch;